@font-face {
  font-family: 'painter';
  src: url("../fonts/Painter.ttf");
  /* IE9 Compat Modes */ }

@font-face {
  font-family: "Roboto";
  src: local(Roboto Thin), url("../fonts/roboto/Roboto-Thin.woff2") format("woff2"), url("../fonts/roboto/Roboto-Thin.woff") format("woff");
  font-weight: 100; }

@font-face {
  font-family: "Roboto";
  src: local(Roboto Light), url("../fonts/roboto/Roboto-Light.woff2") format("woff2"), url("../fonts/roboto/Roboto-Light.woff") format("woff");
  font-weight: 300; }

@font-face {
  font-family: "Roboto";
  src: local(Roboto Regular), url("../fonts/roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: "Roboto";
  src: local(Roboto Medium), url("../fonts/roboto/Roboto-Medium.woff2") format("woff2"), url("../fonts/roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500; }

@font-face {
  font-family: "Roboto";
  src: local(Roboto Bold), url("../fonts/roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/roboto/Roboto-Bold.woff") format("woff");
  font-weight: 700; }

html {
  background: #4a5f9d;
  background: -moz-linear-gradient(top, #4a5f9d 0%, #202f61 32%, #2f5e8c 59%, #479ec3 85%, #adb9cb 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, #4a5f9d), color-stop(32%, #202f61), color-stop(59%, #2f5e8c), color-stop(85%, #479ec3), color-stop(100%, #adb9cb));
  background: -webkit-linear-gradient(top, #4a5f9d 0%, #202f61 32%, #2f5e8c 59%, #479ec3 85%, #adb9cb 100%);
  background: -o-linear-gradient(top, #4a5f9d 0%, #202f61 32%, #2f5e8c 59%, #479ec3 85%, #adb9cb 100%);
  background: -ms-linear-gradient(top, #4a5f9d 0%, #202f61 32%, #2f5e8c 59%, #479ec3 85%, #adb9cb 100%);
  background: linear-gradient(to bottom, #4a5f9d 0%, #202f61 32%, #2f5e8c 59%, #479ec3 85%, #adb9cb 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4a5f9d', endColorstr='#adb9cb', GradientType=0 );
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-width: 600px; }

body {
  position: absolute;
  z-index: 99;
  background: url("../img/wdots.png");
  background-size: 40px 40px;
  width: 99%;
  height: 98.8%;
  margin-right: 10px !important; }

#mainImg {
  border: 5pt double #202f61; }

.publicityWrap {
  border: 2pt solid #202f61; }

#mainImg:after {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  right: -15px;
  bottom: -15px;
  background: red;
  z-index: -1; }

.row {
  display: flex;
  flex-flow: 'row' 'wrap'; }

.logo {
  width: 200px;
  height: 50px;
  position: absolute;
  background: white;
  border: 1pt solid black;
  display: block;
  margin: 0 auto;
  font-family: 'painter';
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  -webkit-box-shadow: 0 15px 10px #000;
  -moz-box-shadow: 0 15px 10px #000;
  box-shadow: 0 15px 10px #000;
  -webkit-transform: rotate(-3deg);
  -moz-transform: rotate(-3deg);
  -o-transform: rotate(-3deg);
  -ms-transform: rotate(-3deg);
  transform: rotate(-3deg); }

h1, h2, h3, a {
  font-family: 'Roboto', sans-serif;
  font-weight: 700; }

.cartel {
  flex: 4;
  margin: 10px; }

.card {
  margin-top: 20px;
  margin-right: 20px;
  min-height: 200px;
  min-width: 200px;
  flex: 1;
  border: 3pt solid #202f61;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75); }

.publicity {
  flex: 1;
  margin: 10px auto;
  padding: 5px; }

.description {
  flex: 1;
  min-height: 100%;
  margin: 10px;
  padding: 5px; }

.a {
  border-top-left-radius: 20px; }

.b {
  border-top-right-radius: 20px; }

section {
  background: white;
  width: 90%;
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  background: #FFF;
  margin: 40px auto;
  padding: 20px; }

.video-container {
  border-top-left-radius: 20px;
  overflow: hidden;
  width: 70%; }

.video-description {
  width: 30%;
  position: relative;
  float: left; }

.main {
  padding: 20px; }

/*==================================================
 * Effect 2
 * ===============================================*/
.effect2 {
  position: relative; }

.effect2:before, .effect2:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width: 300px;
  background: #000;
  -webkit-box-shadow: 0 15px 10px #000;
  -moz-box-shadow: 0 15px 10px #000;
  box-shadow: 0 15px 10px #000;
  -webkit-transform: rotate(-3deg);
  -moz-transform: rotate(-3deg);
  -o-transform: rotate(-3deg);
  -ms-transform: rotate(-3deg);
  transform: rotate(-3deg); }

.effect2:after {
  -webkit-transform: rotate(3deg);
  -moz-transform: rotate(3deg);
  -o-transform: rotate(3deg);
  -ms-transform: rotate(3deg);
  transform: rotate(3deg);
  right: 10px;
  left: auto; }
